import React, { createContext, useContext, useState } from 'react';
const PasswordContext = createContext(undefined);
const PasswordProvider = ({ children }) => {
    const [passwordLength, setPasswordLength] = useState(20);
    const [passwordResult, setResult] = useState("Click generate button");
    const [toggleStates, setToggleStates] = useState({
        includeUpperCase: true,
        includeLowerCase: true,
        includeNumbers: true,
        includeSymbols: true,
    });
    const handleSliderChange = (value) => {
        return setPasswordLength(value);
    };
    const handleToggleChange = (key, value) => {
        return setToggleStates((prev) => (Object.assign(Object.assign({}, prev), { [key]: value })));
    };
    const handleResult = (result) => {
        return setResult(result);
    };
    return (React.createElement(PasswordContext.Provider, { value: {
            passwordLength,
            toggleStates,
            passwordResult,
            handleSliderChange,
            handleToggleChange,
            handleResult
        } }, children));
};
export default PasswordProvider;
export const usePasswordContext = () => {
    const context = useContext(PasswordContext);
    if (!context) {
        throw new Error('usePasswordContext must be used within a PasswordProvider');
    }
    return context;
};
